export default {
	data() {
		return {
			source: 0,
			uid:0,
			addressList: []
		}
	},
	onLoad(option){
		this.uid = option.uid;
		let _this = this;
		uni.request({
		    url: this.apiServer + "/api/address/listByUid?uid="+_this.uid,
		    dataType: "JSON",
		    success: function(res) {
				const result = res.data;
				result.sort(function(a,b){
					return a.addressStatus - b.addressStatus;
				});
				_this.addressList = result;
			},
		});
	},
	methods: {
		//选择地址
		checkAddress(item){
			if(this.source == 1){
				//this.$api.prePage()获取上一页实例，在App.vue定义
				this.$api.prePage().addressData = item;
				uni.navigateBack()
			}
		},
		addAddress(type, item){
			let id = "";
			let status = "";
			if(item){
				id = item.id;
				status = item.addressStatus;
			}
			uni.navigateTo({
				url: `/pages/address/addressManage?type=${type}&uid=${this.uid}&id=${id}&status=${status}`
			})
		},
		//添加或修改成功之后回调
		refreshList(data, type){
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			this.addressList.unshift(data);
			console.log(data)
		}
	}
}
